*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: regofont;
  src: url("regofont.559e0edd.ttf") format("truetype");
}

@font-face {
  font-family: morrissans;
  src: url("morrissans.85d9e079.ttf") format("truetype");
}

@font-face {
  font-family: serioucity;
  src: url("serioucity.e47c9980.ttf") format("truetype");
}

@font-face {
  font-family: herticalserif;
  src: url("herticalserif.6bc7e820.otf") format("opentype");
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  overflow-x: hidden;
}

@media screen and (max-width: 600px) {
  html {
    font-size: 50%;
  }
}

body {
  color: #333;
  min-height: 100vh;
  background-image: linear-gradient(112.96deg, #ffec9f 10.11%, #c9a4db 88.12%);
  grid-template-rows: 6rem auto auto;
  grid-template-columns: 1fr minmax(300px, 1200px) 1fr;
  font-family: Arial, Helvetica, sans-serif, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  display: grid;
  overflow-x: hidden;
}

main {
  -webkit-backdrop-filter: blur(200px);
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(200px);
  box-shadow: var(--drop-shadow-lg);
  z-index: 2;
  border: 1px solid rgba(100, 100, 100, .247);
  border-radius: 15px;
  grid-column: 2 / 3;
  position: relative;
}

.page__header {
  max-width: 100%;
  -webkit-backdrop-filter: blur(200px);
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(600px);
  box-shadow: var(--drop-shadow-lg);
  z-index: 1;
  border: 1px solid rgba(100, 100, 100, .247);
  border-radius: 20px;
  grid-column: 2 / 3;
  justify-content: space-between;
  padding: 1rem;
  line-height: 1.2;
  display: flex;
}

.remove-text, .remove-it {
  display: none;
}

.clr_act {
  background-color: var(--bg-color-act);
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: initial;
  }

  100% {
    opacity: 0;
    display: none;
  }
}

@keyframes fadeOut {
  0% {
    opacity: initial;
  }

  100% {
    opacity: 0;
    display: none;
  }
}

.show {
  -webkit-animation: fadeIn .5s cubic-bezier(.37, 0, 0, 1.01) forwards;
  animation: fadeIn .5s cubic-bezier(.37, 0, 0, 1.01) forwards;
  display: flex;
}

.hidden {
  -webkit-animation: fadeOut .5s cubic-bezier(.37, 0, 0, 1.01) forwards;
  animation: fadeOut .5s cubic-bezier(.37, 0, 0, 1.01) forwards;
  display: none;
}

.hidden--instant {
  display: none;
}

@-webkit-keyframes slideOutRight {
  100% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }
}

@keyframes slideOutRight {
  100% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }
}

@-webkit-keyframes slideOutLeft {
  0% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }

  100% {
    -webkit-transform: translateX(300%);
    transform: translateX(300%);
  }
}

@keyframes slideOutLeft {
  0% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }

  100% {
    -webkit-transform: translateX(300%);
    transform: translateX(300%);
  }
}

@-webkit-keyframes rippleClick {
  0% {
    opacity: 1;
    height: 0;
    width: 0;
  }

  100% {
    opacity: 0;
    height: 500%;
    width: 500%;
  }
}

@keyframes rippleClick {
  0% {
    opacity: 1;
    height: 0;
    width: 0;
  }

  100% {
    opacity: 0;
    height: 500%;
    width: 500%;
  }
}

.slide-out--right {
  -webkit-animation: slideOutRight .5s cubic-bezier(.175, .885, .32, 1.1) forwards;
  animation: slideOutRight .5s cubic-bezier(.175, .885, .32, 1.1) forwards;
}

.slide-out--left {
  -webkit-animation: slideOutLeft .5s cubic-bezier(.175, .885, .32, 1.1) forwards;
  animation: slideOutLeft .5s cubic-bezier(.175, .885, .32, 1.1) forwards;
}

.click_clr {
  position: relative;
  overflow: hidden;
}

.ripples {
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  -webkit-animation: rippleClick .5s linear;
  animation: rippleClick .5s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@-webkit-keyframes animateMenuForwards {
}

@keyframes animateMenuForwards {
}

.animateMenu {
  -webkit-animation: animateMenu .5s cubic-bezier(.25, .46, .45, .94) forwards;
  animation: animateMenu .5s cubic-bezier(.25, .46, .45, .94) forwards;
}

.btn {
  text-transform: uppercase;
  color: var(--text-color-primary);
  background-color: var(--bg-color-mid);
  box-shadow: var(--drop-shadow-sm);
  border-style: none;
  border-radius: 3px;
  padding: 1rem;
  font-size: 1.6rem;
  text-decoration: underline;
  transition: all .2s;
}

.btn__reset {
  aspect-ratio: 1 / 1;
}

.btn:hover {
  background-color: var(--text-color-secondary);
  color: var(--text-color-primary);
  cursor: pointer;
  box-shadow: var(--drop-shadow-sm-1);
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
}

.btn:active {
  box-shadow: var(--drop-shadow-sm-2);
  -webkit-transform: translateY(3px);
  transform: translateY(3px);
}

.on-off__sw {
  width: 5.6rem;
  height: 3rem;
  margin-right: 1rem;
  padding-bottom: 3rem;
  padding-right: 5.6rem;
  position: relative;
}

.on-off__sw--btn {
  display: none;
}

.on-off__sw--btn:checked ~ .on-off__sw--span:after {
  left: calc(100% - 2px - 2.6rem);
}

.on-off__sw--btn:checked ~ .on-off__sw--span:before {
  background-color: var(--bg-color-on);
}

.on-off__sw--span:before {
  content: "";
  width: 5.6rem;
  height: 3rem;
  background-color: var(--bg-color-mid);
  box-shadow: var(--drop-shadow-sm);
  border-radius: 5rem;
  position: absolute;
  top: 0;
  left: 0;
}

.on-off__sw--span:after {
  content: "";
  width: 2.6rem;
  height: 2.6rem;
  background-color: #fff;
  border-radius: 5rem;
  transition: all .3s cubic-bezier(.175, .885, .32, 1.175);
  position: absolute;
  top: 2px;
  left: 2px;
  box-shadow: 0 0 8px 3px rgba(0, 0, 0, .07);
}

:root {
  --text-color-primary: #494949;
  --text-color-secondary: #ff8383;
  --bg-color-light: #f2f2f2;
  --bg-color-dark: #494949;
  --bg-color-mid: #d0d0d0;
  --bg-color-act: #ff8383;
  --bg-color-on: #9fff92;
  --art-accent-clr: #fff;
  --art-background-clr: #000;
  --gradient-color-primary: linear-gradient( 94.57deg, #d9d9d9 33.34%, #e6e6e6 50.04%, #c2c0c0 65.67% );
  --art-font: "regofont";
  --btn-lg: 8rem;
  --btn-md: 6rem;
  --btn-sm: 4rem;
  --drop-shadow-lg: 0px 100px 80px rgba(0, 0, 0, .07), 0px 41.7776px 33.4221px rgba(0, 0, 0, .05), 0px 22.3363px 17.869px rgba(0, 0, 0, .043), 0px 12.5216px 10.0172px rgba(0, 0, 0, .035), 0px 6.6501px 5.32008px rgba(0, 0, 0, .027), 0px 2.76726px 2.21381px rgba(0, 0, 0, .02);
  --drop-shadow-sm: 0px 0px 10px rgba(0, 0, 0, .1);
  --drop-shadow-sm-1: 5px 10px 10px rgba(0, 0, 0, .3);
  --drop-shadow-sm-2: 2px 2px 5px rgba(0, 0, 0, .3);
  --drop-shadow-md: 0px 100px 80px rgba(0, 0, 0, .02), 0px 41.7776px 33.4221px rgba(0, 0, 0, .027), 0px 22.3363px 17.869px rgba(0, 0, 0, .035), 0px 12.5216px 10.0172px rgba(0, 0, 0, .043), 0px 6.6501px 5.32008px rgba(0, 0, 0, .05), 0px 2.76726px 2.21381px rgba(0, 0, 0, .07);
  --text-color-black: #000;
  --text-color-white: #fff;
  --text-color-red: red;
  --text-color-green: #0f0;
  --text-color-blue: #00f;
  --text-color-yellow: #ff0;
  --text-color-pink: #f0f;
  --bg-color-black: #000;
  --bg-color-white: #fff;
  --bg-color-red: red;
  --bg-color-green: #0f0;
  --bg-color-blue: #00f;
  --bg-color-yellow: #ff0;
  --bg-color-pink: #f0f;
}

.container__grid {
  grid-gap: 1rem;
  grid-template-rows: repeat(3, auto);
  grid-template-columns: auto;
  justify-items: stretch;
  padding: 1rem;
  display: grid;
}

.container__grid__item--col-styles {
  z-index: 1;
  grid-area: 1 / 1 / span 1 / span 1;
  position: relative;
}

.container__grid__item--text {
  z-index: 11;
  grid-area: 3 / 1 / span 1 / span 1;
  position: relative;
}

@media screen and (min-width: 600px) {
  .container__grid {
    grid-template-columns: 1fr 1fr;
  }

  .container__grid__item--col-styles {
    z-index: 1;
    grid-area: 2 / 1 / span 1 / span 2;
    position: relative;
  }

  .container__grid__item--text {
    z-index: 11;
    grid-area: 3 / 1 / span 1 / span 2;
    position: relative;
  }
}

.controls__text {
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

@media screen and (min-width: 800px) {
  .controls__text {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }
}

footer {
  grid-area: 3 / 2;
  justify-content: center;
  margin-top: auto;
  display: flex;
}

.artboard {
  min-height: 15rem;
  box-shadow: var(--drop-shadow-lg);
  z-index: 10;
  touch-action: none;
  background-color: #fff;
  border-radius: 5px;
  grid-area: 2 / 1 / span 1 / span 1;
  align-items: center;
  justify-items: center;
  display: grid;
  position: relative;
}

@media screen and (min-width: 600px) {
  .artboard {
    min-height: 30rem;
    z-index: 10;
    grid-area: 1 / 1 / span 1 / span 2;
    position: relative;
  }
}

.art {
  font-family: var(--art-font);
  min-width: 270px;
  aspect-ratio: 3.14;
  text-align: center;
  background-color: #000;
  border-radius: 10px;
  justify-content: center;
  position: relative;
}

.art .outter__line {
  width: 266px;
  height: 82px;
  border-radius: 9px;
}

.art .inner__line {
  width: 260px;
  height: 76px;
  border-radius: 7px;
}

.art .outter__line, .art .inner__line {
  border: 1px solid var(--art-accent-clr);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 600px) {
  .art {
    scale: 2;
  }
}

.outlineRed {
  outline: 1px dashed rgba(255, 0, 0, .5);
}

.text__content {
  color: var(--art-accent-clr);
  text-transform: uppercase;
  cursor: pointer;
  z-index: 1000;
  text-orientation: upright;
  text-align: center;
  position: absolute;
}

.text__content__middle {
  white-space: nowrap;
  text-align: center;
  min-width: 80%;
  min-height: 45%;
  vertical-align: text-bottom;
  font-size: 5rem;
  top: 25%;
  left: 10%;
}

.text__content__top {
  letter-spacing: 3px;
  white-space: nowrap;
  min-width: 80%;
  min-height: 14px;
  top: 10%;
  left: 10%;
}

.text__content__left {
  writing-mode: vertical-lr;
  text-orientation: upright;
  white-space: nowrap;
  min-height: 80%;
  min-width: 10px;
  top: 10%;
  left: 3%;
}

.text__content__bottom {
  min-height: 10%;
  min-width: 80%;
  letter-spacing: 3px;
  white-space: nowrap;
  padding-bottom: .5rem;
  top: 80%;
  left: 10%;
}

@media screen and (min-width: 600px) {
  .text__content:hover {
    outline: 1px dashed rgba(255, 0, 0, .5);
  }

  .text__content__middle {
    font-size: 4rem;
  }
}

#middle__text--movespan, #top__text--movespan, #left__text--movespan, #bottom__text--movespan {
  width: 30px;
  height: 30px;
  background-color: var(--art-accent-clr);
  cursor: move;
  opacity: 0;
  border: 1px solid #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  position: absolute;
}

#middle__text--movespan {
  top: 30%;
  right: -40px;
}

#top__text--movespan {
  top: -100%;
  right: -30px;
}

#left__text--movespan {
  top: 100%;
  left: -30px;
}

#bottom__text--movespan {
  top: 80%;
  right: -30px;
}

.move__svg {
  width: 12px;
  aspect-ratio: 1 / 1;
}

@media screen and (max-width: 600px) {
  .move__svg {
    display: none;
  }
}

.span--x {
  z-index: 1000;
  border: .5px dashed var(--art-accent-clr);
  width: 100%;
  opacity: .75;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.span--x--1 {
  top: 35%;
}

.span--x--2 {
  top: 65%;
}

.span--y {
  z-index: 100;
  height: 100%;
  border: .5px dashed var(--art-accent-clr);
  opacity: .75;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.span--y--1 {
  left: 40%;
}

.span--y--2 {
  left: 60%;
}

.span--y--3 {
  left: 70%;
}

.span--y--4 {
  left: 30%;
}

@media screen and (max-width: 450px) {
  .span--x {
    z-index: 1000;
    border: .5px dashed var(--art-accent-clr);
    width: 100%;
    opacity: .75;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  .span--x--1 {
    top: 35%;
  }

  .span--x--2 {
    top: 65%;
  }

  .span--y--1 {
    left: 35%;
  }

  .span--y--2 {
    left: 65%;
  }

  .span--y--3 {
    left: 80%;
  }

  .span--y--4 {
    left: 20%;
  }
}

.controls__input__container {
  width: 100%;
  grid-gap: 1.5rem;
  grid-area: 1 / 1 / span 1 / -1;
  grid-template-rows: auto;
  grid-template-columns: 1fr;
  align-items: center;
  display: grid;
}

@media screen and (min-width: 600px) {
  .controls__input__container {
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: stretch;
  }
}

.controls__input {
  -webkit-backdrop-filter: blur(200px);
  backdrop-filter: blur(200px);
  -webkit-backdrop-filter: blur(200px);
  border: 1px solid rgba(100, 100, 100, .247);
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  padding: 1.6rem;
  font-size: 1.6rem;
  display: flex;
}

.controls__input--colour {
  grid-gap: 1.5rem;
  grid-row: 1 / span 1;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.controls__input--style {
  grid-row: 2 / span 1;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.controls__input--style--box {
  justify-content: space-between;
  display: flex;
  overflow: hidden;
}

.controls__input--style--box > input {
  display: none;
}

.controls__input--style--controls {
  height: var(--btn-sm);
  width: var(--btn-sm);
  background-color: var(--bg-color-mid);
  z-index: 1;
  border: 1px solid #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all .3s cubic-bezier(.165, .84, .44, 1);
  display: flex;
  position: relative;
  -webkit-transform: translateX(200%);
  transform: translateX(200%);
}

.controls__input--style--pos__controls {
  align-items: center;
  display: flex;
}

.controls__input--style--pos__controls > label {
  height: var(--btn-sm);
  width: var(--btn-sm);
  z-index: 1;
  border: 1px solid #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  -webkit-transform: translateX(300%);
  transform: translateX(300%);
}

.controls__input--style--pos__controls > input {
  display: none;
}

.controls__input--style--font-size {
  color: #fff;
  text-shadow: -5px -5px 2px rgba(0, 0, 0, .8);
  height: var(--btn-sm);
  width: var(--btn-sm);
  z-index: 10;
  box-shadow: var(--drop-shadow-sm) inset;
  border: 1px solid #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  display: flex;
  position: relative;
}

.controls__input--text4 {
  flex-direction: row;
  flex: 0 1px;
  justify-content: flex-start;
  display: flex;
}

.menu-span {
  width: 50%;
  height: 2px;
  background-color: var(--bg-color-act);
  color: var(--bg-color-act);
  transition: all 2s cubic-bezier(.23, 1, .32, 1);
  position: relative;
}

.menu-span:after, .menu-span:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: currentColor;
  position: absolute;
}

.menu-span:after {
  top: 6.5px;
}

.menu-span:before {
  bottom: 6.5px;
}

.menu-closed {
  color: #fff;
  background-color: #fff;
  -webkit-transform: rotateZ(90deg);
  transform: rotateZ(90deg);
}

.menu-closed:after {
  top: 0;
}

.menu-closed:before {
  bottom: 0;
}

.controls--check-btn:checked ~ .controls__input--style--controls {
  background-color: var(--text-color-secondary);
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
}

#DragBtnX:checked ~ .pos__controls--x, #DragBtnY:checked ~ .pos__controls--y {
  background-color: var(--text-color-secondary);
  color: #fff;
}

.label--style {
  margin-right: auto;
}

@media screen and (min-width: 600px) {
  .controls__input--colour {
    grid-area: 1 / 1 / span 1 / span 1;
  }

  .controls__input--style {
    grid-area: 1 / 2 / span 1 / -1;
  }

  .controls__input--text4 {
    flex-wrap: wrap;
    flex-basis: fill;
    flex-grow: 1;
    flex-shrink: 1;
    display: flex;
  }
}

.middle__txt--container, .side__txt--container, .top__txt--container, .bottom__txt--container {
  position: relative;
}

.controls__text--input__field {
  box-shadow: var(--drop-shadow-md);
  width: 100%;
  border: 1px solid red;
  border-radius: 5px;
  flex-grow: 1;
  flex-shrink: 1;
  padding: 1rem;
  position: relative;
}

.controls__text--input__field:focus {
  border: 1px solid var(--bg-color-on);
}

.font__block {
  text-align: center;
  color: var(--text-color-primary);
  flex-direction: column;
  font-size: 1.4rem;
  display: flex;
}

.font__block--size__wrapper {
  width: 100%;
  flex-grow: 1;
  flex-shrink: 1;
  margin-top: 1rem;
}

@media screen and (min-width: 600px) {
  .middle__txt--container, .side__txt--container, .top__txt--container, .bottom__txt--container {
    flex-grow: 1;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 600px) {
  .font__block--size__wrapper {
    flex-grow: 0;
    flex-shrink: 20;
    margin-left: 1rem;
  }
}

.btn__clear__txt {
  cursor: pointer;
  color: #fff;
  background-color: var(--bg-color-mid);
  height: 1.6rem;
  aspect-ratio: 1 / 1;
  border-style: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .2s;
  display: flex;
  position: absolute;
  top: 50%;
  right: 5%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.btn__clear__txt span {
  font-size: 1.2rem;
  -webkit-transform: translate(.3px, -1.1px);
  transform: translate(.3px, -1.1px);
}

@media screen and (min-width: 600px) {
  .btn__clear__txt {
    position: absolute;
    right: 3%;
  }

  .btn__clear__txt:active {
    -webkit-transform: translateY(calc(2px - 50%));
    transform: translateY(calc(2px - 50%));
  }

  .btn__clear__txt:hover {
    background-color: var(--bg-color-mid);
    color: var(--text-color-primary);
    cursor: pointer;
    box-shadow: var(--drop-shadow-sm);
